html {
	height: 100%; }

.App {
	position: relative;
	max-width: 1440px;
	margin: 0 auto;
	margin-top: 71px;
	&.-payment {
		padding-bottom: 78px;
		min-height: calc(100vh - 71px);
		@include sm() {
			margin-top: 71px; }
		@include xs() {
			min-height: calc(100vh - 71px);
			padding-bottom: 108px;
 } }			// border: solid 1px red
	@include sm() {
		width: auto;
		max-width: 1024px;
		margin-top: 60px; }
	@include xs(); }
